import { defineStore } from 'pinia';
import { ref, computed } from 'vue';
export const useContactStore = defineStore('contactStore', () => {
    const contacts = ref(JSON.parse(localStorage.getItem('contacts') || '[]'));
    const searchQuery = ref('');
    // getters
    const filteredContacts = computed(() => {
        return (contacts.value.filter(contact => contact.name.toLowerCase().includes(searchQuery.value.toLowerCase())) || []);
    });
    const fetchContacts = async () => {
        if (contacts.value.length === 0) {
            try {
                const response = await fetch('/mockData.json');
                const data = await response.json();
                contacts.value = data;
                saveContacts(); // Сохраняем в localStorage
            }
            catch (error) {
                console.error('Ошибка при загрузке контактов:', error);
            }
        }
    };
    // actions
    const addContact = (contact) => {
        contacts.value.push(contact);
        saveContacts();
    };
    const editContact = (updatedContact) => {
        const index = contacts.value.findIndex(contact => contact.id === updatedContact.id);
        if (index !== -1) {
            contacts.value[index] = updatedContact;
            saveContacts();
        }
    };
    const deleteContact = (id) => {
        contacts.value = contacts.value.filter(contact => contact.id !== id);
        saveContacts();
    };
    const setSearchQuery = (query) => {
        searchQuery.value = query;
    };
    // localStorage
    const saveContacts = () => {
        localStorage.setItem('contacts', JSON.stringify(contacts.value));
    };
    return {
        contacts,
        searchQuery,
        filteredContacts,
        fetchContacts,
        addContact,
        editContact,
        deleteContact,
        setSearchQuery
    };
});
